import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import noData from '@/components/noData'
import back from '@/components/back'
import goodsNav from '@/page/prefRecommend/goodsNav'
import goodsArea from '@/page/prefRecommend/goodsArea'
import { introAdmin } from '../../config'
export default {
    components:{
		swiper,
		swiperSlide,
        noData,
        back,
		goodsNav,
		goodsArea
    },
    data(){
        return{
			user: this.$store.state.userInfo,
			page: 1,
			haveMore: true,
			ListDetails:this.$store.state.preList,
			activeTab: 0,
			pageShow: false,
			fromIM:this.$route.query.fromIM?true:false,
            showConfirm:false,
			thisId:null,
			imgArr2:[],
			MerchantList:[],
			total:0,
			list:[],
			ChannelList:[],
			scrollTopDistance: 0,
			navNum:this.$store.state.navNum,
			tZIndex:false,
			introAdmin:introAdmin,
			AreaId:0,
			AreaName:'',
			swiperBanner: {
				pagination: {
					el: '.banner-pagination'
				}
			},
			swiperGoods: {
				slidesPerView: 2.5,
				spaceBetween: 0,
				freeMode: true,
			}
        }
    },
    created(){
		this.AreaId = this.$store.state.inspctArea?this.$store.state.inspctArea.AreaId:0
		this.AreaName = this.$store.state.inspctArea?this.$store.state.inspctArea.AreaName:''
		this.getMallMerchantInfoPage(false,this.AreaId,this.AreaName)

		this.getGoodsType(this.AreaId,this.AreaName)
		this.getBanner()
		this.GetMallChannelList(this.AreaId,this.AreaName)
    },
    mounted(){
		//this.ChildArea()
    },
    beforeDestroy(){
        window.onscroll = null
    },
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		if(from.meta.keepAlive) {
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		this.$store.dispatch('pushrecomMsg',{
			DataSource: 3,//(123)
			DataId: 0,//Id
			fromIM:this.fromIM
		})
		next()
	},
	beforeRouteEnter(to, from, next) { //
		next(vm => {
			vm.getMore()
			vm.navNum = vm.$store.state.navNum
			let flag = false
			if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || !vm.$store.state.prevUrl || vm.$store.state.isFresh) {
				flag = true
			}
			if(flag) {
				if(vm.$store.state.inspctArea){
					vm.AreaId = vm.$store.state.inspctArea?vm.$store.state.inspctArea.AreaId:0
					vm.AreaName = vm.$store.state.inspctArea?vm.$store.state.inspctArea.AreaName:''
					vm.page = 1
					vm.haveMore = true
					vm.getMallMerchantInfoPage(false,vm.AreaId,vm.AreaName)
					vm.getGoodsType(vm.AreaId,vm.AreaName)
					vm.getBanner()
					vm.GetMallChannelList(vm.AreaId,vm.AreaName)
				}

			} else {
				setTimeout(() => {
					document.documentElement.scrollTop = to.meta.scollTopPosition
				}, 1)
			}

		});
	},
    methods: {
		ChildArea(data){
			this.AreaId = data.AreaId
			this.AreaName = data.AreaName
			this.getMallMerchantInfoPage(false,data.AreaId,data.AreaName)
			this.getGoodsType(data.AreaId,data.AreaName)
			this.getBanner()
			this.GetMallChannelList(data.AreaId,data.AreaName)
		},
		goAllGoods(i,v){
			let arr = {
				MenuType:i,
				GoodsTypeNo:v
			}
			this.$store.dispatch('pushnavNum',1)
			this.$store.dispatch('pushsearchText',null)
			this.$store.dispatch('pushnumMenu',arr)
			this.$router.push(`/allGoods`)
		},
		searchList(){
			this.$router.push(`/prefRecommend/preSpecialtyList`)
		},
		getBanner(){
			this.$get('PlatFormAPI/SpecialistHosGroup/GetMallCarouselBanners',{}).then(o => {
				this.imgArr2 = o
			})
		},
		goUrl(item){
			//APP
			//this.$router.push(`/vipEntey?AddId=1&GoodsTypeNo=999995`)
			//return
			if(item.TargetUrl){
				let _url = item.TargetUrl
				if(_url.indexOf('amp') != -1){
					_url = _url.replace("amp;","")
				}
				if(_url.indexOf('AddId') != -1){
					let obj = {
						title : "vip",
						linkUrl : _url
					}
					this.$bridge.callhandler('toRecommendInsideLink', JSON.stringify(obj))  
				}else{
					let obj = {
						title : "",
						linkUrl : _url
					}
					this.$bridge.callhandler('ServiceIntroduction', JSON.stringify(obj))
				}
				console.log(_url)
			}
		},
		getMallMerchantInfoPage(h,data,AreaName){
			if(this.haveMore){
				this.$post('PlatFormAPI/SpecialistHosGroup/GetMallMerchantInfoPage',{
					PageIndex:this.page,
					PageSize:10,
					AreaId:data,
					AreaName:AreaName,
				}).then(o => {
					if (!o.ReturnData || o.ReturnData.length < 10){
						this.haveMore = false
					}
					else {
						this.haveMore = true
					}
					if (h){
						o.ReturnData.forEach(v => {
							this.MerchantList.push(v)
						})
					}
					else {
						this.MerchantList = o.ReturnData
					}
					if(this.MerchantList.length < o.Total){
						this.haveMore = true
					}

				})
			}
		},
		getGoodsType(AreaId,AreaName){
			this.$get('PlatFormAPI/SpecialistHosGroup/GetMallGoodsType',{
				AreaId,
				AreaName,
			}).then(o => {
				this.list = o
			})
		},
		goMerchant(i){
			this.$router.push("/businessInfo?MerchantId="+i.merchant_id+"&num="+i.goodsCount)
		},
		GetMallChannelList(data,AreaName){
			this.$get('PlatFormAPI/SpecialistHosGroup/GetMallChannelList',{
				DoctorId:this.user.Data.User.DoctorId,
				AreaId:data,
				AreaName:AreaName,
			}).then(o => {
				this.ChannelList = o
			})
		},

        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
					const top = document.documentElement.scrollTop
						|| window.pageYOffset
						|| document.body.scrollTop,
					h = el.scrollHeight,
					wh = window.innerHeight
					this.scrollTopDistance = top;
					if(top >40){
						this.tZIndex = true
					}else{
						this.tZIndex = false
					}
                    if (h - top - wh < 10 && this.haveMore){
                        this.page += 1
						this.getMallMerchantInfoPage(true,this.AreaId,this.AreaName)
                    }
                }, 100)
            }
        },
        goIntro(){
			//APP
			let _url = this.introAdmin + "index.html#/prefIntro?fromAPP=1"
			let obj = {
				title : "",
				linkUrl : _url
			}
			this.$bridge.callhandler('ServiceIntroduction', JSON.stringify(obj))
		}
    }
}



